import HeaderSetting from "./components/Header";
import React, { useState } from "react";

function TambahSosialMedia() {

  const [isOpen, setIsOpen] = useState(false);
  const [selectedValue, setSelectedValue] = useState("");

  const options = ["Option 1", "Option 2", "Option 3", "Option 4"];

  const toggleDropdown = () => setIsOpen(!isOpen);

  const handleOptionClick = (option) => {
    setSelectedValue(option);
    setIsOpen(false); // Tutup dropdown setelah memilih
  };

    return(
        <div>
            <HeaderSetting label="Data sosial media" />

            <div className="container bg-white pt-24 pb-24">
                <div className="d-flex flex-column">

                <div className="position-relative" style={{ width: "300px" }}>
                    {/* Input dengan Icon */}
                    <div
                        className="input-group"
                        onClick={toggleDropdown}
                        style={{ cursor: "pointer" }}
                    >
                        <input
                        type="text"
                        className="form-control"
                        placeholder="Select an option"
                        value={selectedValue}
                        readOnly
                        />
                        <div className="input-group-append">
                        <span className="input-group-text bg-white">
                            <i
                            className={`fa ${isOpen ? "fa-angle-down" : "fa-angle-right"}`}
                            ></i>
                        </span>
                        </div>
                    </div>

                    {/* Dropdown List */}
                    {isOpen && (
                        <div
                        className="dropdown-menu show"
                        style={{
                            position: "absolute",
                            top: "100%",
                            left: "0",
                            width: "100%",
                            zIndex: "1050",
                        }}
                        >
                        {options.map((option, index) => (
                            <button
                            key={index}
                            className="dropdown-item"
                            onClick={() => handleOptionClick(option)}
                            >
                            {option}
                            </button>
                        ))}
                        </div>
                    )}
                    </div>

                    <span className="text-xs">Username</span>
                    <input className="form-control text-xs-small border-0 bg-gray input-gray"
                    placeholder="">
                    </input>
                    <span className="text-xs">Jumlah Follower</span>
                    <input className="form-control text-xs-small border-0 bg-gray input-gray"
                    placeholder=""></input>
                </div>
            </div>
        </div>
    )
}

export default TambahSosialMedia;